import React from "react";
import Slider from "react-slick";
import Card from "@mui/material/Card";
import Typography from "@mui/material/Typography";
import { Link } from "react-router-dom";
import MCD from "../assets/MCD.webp";
import Costa from "../assets/Costa.webp";
import DQueen from "../assets/DQueen.webp";
import Cinepolis from "../assets/Cinepolis.webp";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import Divider from "@mui/material/Divider";
import WifiIcon from "@mui/icons-material/Wifi";
import CardGiftcardIcon from "@mui/icons-material/CardGiftcard";
import PhonelinkRingIcon from "@mui/icons-material/PhonelinkRing";
import LeftArrow from "../assets/leftArrow.png";
import RightArrow from "../assets/rightArrow.png";
import { Box, Button } from "@mui/material";

const PlanCards = ({ handleSelctedPlan }) => {
  function NextArrow(props) {
    const { className, onClick } = props;
    return (
      <div className={className} onClick={onClick}>
        <img src={RightArrow} alt="RightArrow" />
      </div>
    );
  }

  function PrevArrow(props) {
    const { className, onClick } = props;
    return (
      <div className={className} onClick={onClick}>
        <img src={LeftArrow} alt="LeftArrow" />
      </div>
    );
  }

  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 3,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
  };
  return (
    <>
      <h2>Explore your choices </h2>
      <Typography sx={{ fontSize: "16px", marginBottom: "40px" }}>
        And go further
      </Typography>

      <Slider {...settings}>
        <Card variant="outlined" className="plan-card">
          <Typography variant="h4">Postpaid 7</Typography>
          <Link to="/" className="logo-deals">
            <img src={MCD} alt="MCD" />
            <img src={Costa} alt="Costa" />
            <img src={DQueen} alt="DQueen" />
            <img src={Cinepolis} alt="Cinepolis" />
            <Typography variant="body1">+ included deals</Typography>
          </Link>
          <List>
            <ListItem>
              <ListItemIcon>
                <WifiIcon />
              </ListItemIcon>
              <ListItemText primary="20GB Unlimited" secondary="data" />
            </ListItem>
            <Divider />
            <ListItem>
              <ListItemIcon>
                <CardGiftcardIcon />
              </ListItemIcon>
              <ListItemText
                primary="5GB (valid for 3 months)"
                secondary="online exclusive data***"
              />
            </ListItem>
            <Divider />
            <ListItem>
              <ListItemIcon>
                <PhonelinkRingIcon />
              </ListItemIcon>
              <ListItemText primary="600" secondary="locla minutes" />
            </ListItem>
            <Divider />
          </List>
          <Typography
            sx={{
              color: "#ff345f",
              margin: "20px 0",
              textTransform: "uppercase",
            }}
          >
            View Plan Details
          </Typography>
          <Box className="box-select">
            <Box className="plan-card-price-section">
              <Typography className="plan-card-price">
                BD 6.900 <span>/mo</span>
              </Typography>
              <Typography>Subject to 10% VAT</Typography>
            </Box>
            <Button
              className="secondary-button"
              sx={{ minWidth: "178px !important" }}
              onClick={() => handleSelctedPlan("Postpaid 7", 6.9)}
            >
              Select
            </Button>
          </Box>
        </Card>
        <Card variant="outlined" className="plan-card">
          <Typography variant="h4">Postpaid 8</Typography>
          <Link to="/" className="logo-deals">
            <img src={MCD} alt="MCD" />
            <img src={Costa} alt="Costa" />
            <img src={DQueen} alt="DQueen" />
            <img src={Cinepolis} alt="Cinepolis" />
            <Typography variant="body1">+ included deals</Typography>
          </Link>
          <List>
            <ListItem>
              <ListItemIcon>
                <WifiIcon />
              </ListItemIcon>
              <ListItemText primary="30GB Unlimited" secondary="data" />
            </ListItem>
            <Divider />
            <ListItem>
              <ListItemIcon>
                <CardGiftcardIcon />
              </ListItemIcon>
              <ListItemText
                primary="8GB (valid for 12 months)"
                secondary="online exclusive data***"
              />
            </ListItem>
            <Divider />
            <ListItem>
              <ListItemIcon>
                <PhonelinkRingIcon />
              </ListItemIcon>
              <ListItemText primary="500" secondary="local minutes" />
            </ListItem>
            <Divider />
          </List>
          <Typography
            sx={{
              color: "#ff345f",
              margin: "20px 0",
              textTransform: "uppercase",
            }}
          >
            View Plan Details
          </Typography>
          <Box className="box-select">
            <Box className="plan-card-price-section">
              <Typography className="plan-card-price">
                BD 7.900 <span>/mo</span>
              </Typography>
              <Typography>Subject to 10% VAT</Typography>
            </Box>
            <Button
              className="secondary-button"
              sx={{ minWidth: "178px !important" }}
              onClick={() => handleSelctedPlan("Postpaid 8", 7.9)}
            >
              Select
            </Button>
          </Box>
        </Card>
        <Card variant="outlined" className="plan-card">
          <Typography variant="h4">Postpaid 9</Typography>
          <Link to="/" className="logo-deals">
            <img src={MCD} alt="MCD" />
            <img src={Costa} alt="Costa" />
            <img src={DQueen} alt="DQueen" />
            <img src={Cinepolis} alt="Cinepolis" />
            <Typography variant="body1">+ included deals</Typography>
          </Link>
          <List>
            <ListItem>
              <ListItemIcon>
                <WifiIcon />
              </ListItemIcon>
              <ListItemText primary="12GB" secondary="data" />
            </ListItem>
            <Divider />
            <ListItem>
              <ListItemIcon>
                <CardGiftcardIcon />
              </ListItemIcon>
              <ListItemText
                primary="3GB (valid for 38 days)"
                secondary="online exclusive data***"
              />
            </ListItem>
            <Divider />
            <ListItem>
              <ListItemIcon>
                <PhonelinkRingIcon />
              </ListItemIcon>
              <ListItemText primary="600" secondary="locla minutes" />
            </ListItem>
            <Divider />
          </List>
          <Typography
            sx={{
              color: "#ff345f",
              margin: "20px 0",
              textTransform: "uppercase",
            }}
          >
            View Plan Details
          </Typography>
          <Box className="box-select">
            <Box className="plan-card-price-section">
              <Typography className="plan-card-price">
                BD 8.900 <span>/mo</span>
              </Typography>
              <Typography>Subject to 10% VAT</Typography>
            </Box>
            <Button
              className="secondary-button"
              sx={{ minWidth: "178px !important" }}
              onClick={() => handleSelctedPlan("Postpaid 9", 8.9)}
            >
              Select
            </Button>
          </Box>
        </Card>
        <Card variant="outlined" className="plan-card">
          <Typography variant="h4">Postpaid 9</Typography>
          <Link to="/" className="logo-deals">
            <img src={MCD} alt="MCD" />
            <img src={Costa} alt="Costa" />
            <img src={DQueen} alt="DQueen" />
            <img src={Cinepolis} alt="Cinepolis" />
            <Typography variant="body1">+ included deals</Typography>
          </Link>
          <List>
            <ListItem>
              <ListItemIcon>
                <WifiIcon />
              </ListItemIcon>
              <ListItemText primary="9GB" secondary="data" />
            </ListItem>
            <Divider />
            <ListItem>
              <ListItemIcon>
                <CardGiftcardIcon />
              </ListItemIcon>
              <ListItemText
                primary="2GB (valid for 28 days)"
                secondary="online exclusive data***"
              />
            </ListItem>
            <Divider />
            <ListItem>
              <ListItemIcon>
                <PhonelinkRingIcon />
              </ListItemIcon>
              <ListItemText primary="500" secondary="locla minutes" />
            </ListItem>
            <Divider />
          </List>
          <Typography
            sx={{
              color: "#ff345f",
              margin: "20px 0",
              textTransform: "uppercase",
            }}
          >
            View Plan Details
          </Typography>
          <Box className="box-select">
            <Box className="plan-card-price-section">
              <Typography className="plan-card-price">
                BD 6.900 <span>/mo</span>
              </Typography>
              <Typography>Subject to 10% VAT</Typography>
            </Box>
            <Button
              className="secondary-button"
              sx={{ minWidth: "178px !important" }}
            >
              Select
            </Button>
          </Box>
        </Card>
      </Slider>
    </>
  );
};

export default PlanCards;
