import React, { useState } from "react";
import Button from "@mui/material/Button";
import NavigateBeforeIcon from "@mui/icons-material/NavigateBefore";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import { Typography } from "@mui/material";
import DeliveryDetails from "./DeliveryDetails";
import Grid from "@mui/material/Grid";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import DoneIcon from "@mui/icons-material/Done";

const DeliveryPayment = ({ handleClick }) => {
  const [cardValue, setCardValue] = useState("");

  const handleCardChange = (event) => {
    setCardValue(event.target.value);
  };

  const styles = {
    summaryBox: {
      borderRadius: "20px",
      boxShadow: "0 2px 4px 0 rgba(142,154,160,.08)",
      border: "solid 1px rgba(142,154,160,.28)",
      backgroundColor: "#fff",
      margin: "32px",
      padding: "16px 0",
    },
    summaryHeadBox: {
      borderRadius: "20px 20px 0 0",
      boxShadow: "0 2px 4px 0 rgba(142,154,160,.08)",
      border: "solid 1px rgba(142,154,160,.28)",
      backgroundColor: "#fff",
    },
    summaryTitle: {
      fontSize: "19px",
      letterSpacing: "0.5px",
      fontWeight: "bold",
      padding: "20px",
      borderBottom: "1px solid #eee",
    },
    doneIcon: {
      position: "absolute",
      right: "0",
      width: "14px",
      background: "#ff375e",
      borderBottomLeftRadius: "8px",
      borderTopRightRadius: "8px",
      color: "#fff",
      height: "14px",
    },
  };

  return (
    <>
      <Button onClick={() => handleClick(false)} sx={{ color: "#ff375e" }}>
        <NavigateBeforeIcon sx={{ fontSize: "30px" }} /> Go Back
      </Button>
      <Box style={styles.summaryBox} className="delivery-payment">
        <Typography variant="h4" style={styles.summaryTitle}>
          Customer contact details
        </Typography>
        <Box
          component="form"
          sx={{
            m: 1,
            padding: "35px 30px 50px",
          }}
          noValidate
          autoComplete="off"
        >
          <Box
            sx={{
              width: "48%",
              m: 1,
            }}
          >
            <TextField
              id="standard-basic"
              label="Email Address"
              variant="standard"
              defaultValue="test@email.com"
              fullWidth
              InputProps={{
                readOnly: true,
              }}
            />
          </Box>
          <Box
            sx={{
              "& > :not(style)": { m: 1, width: "50%" },
              width: "100%",
              display: "flex",
            }}
          >
            <TextField
              id="standard-basic"
              label="Mobile Number"
              variant="standard"
              defaultValue="36000000"
              InputProps={{
                readOnly: true,
              }}
            />
            <TextField
              id="standard-basic"
              label="Alternative contact number"
              variant="standard"
            />
          </Box>
        </Box>
      </Box>
      <Box style={styles.summaryBox}>
        <Typography variant="h4" style={styles.summaryTitle}>
          Delivery Details
        </Typography>
        <DeliveryDetails />
      </Box>
      <Box style={styles.summaryBox}>
        <Typography variant="h4" style={styles.summaryTitle}>
          How would you like to pay
        </Typography>

        <RadioGroup
          aria-labelledby="demo-row-radio-buttons-group-label"
          name="column-radio-buttons-group"
          className="custom-radio card-radio"
          value={cardValue}
          onChange={handleCardChange}
        >
          <Grid container sx={{ padding: "15px 30px" }} spacing={3}>
            <Grid
              item
              xs={4}
              sx={{
                display: "flex",
                position: "relative",
              }}
            >
              <FormControlLabel
                value="debit"
                control={<Radio />}
                label="Debit Card"
                sx={{ width: "100%" }}
              />
              {cardValue === "debit" && <DoneIcon style={styles.doneIcon} />}
            </Grid>
            <Grid
              item
              xs={4}
              sx={{
                display: "flex",
                position: "relative",
              }}
            >
              <FormControlLabel
                value="credit"
                control={<Radio />}
                label="Credit Card"
                sx={{ width: "100%" }}
              />
              {cardValue === "credit" && <DoneIcon style={styles.doneIcon} />}
            </Grid>
          </Grid>
        </RadioGroup>
      </Box>
      <Box sx={{ marginBottom: "30px" }}>
        <Typography
          sx={{
            fontSize: "16px",
            fontWeight: "bold",
            marginBottom: "5px",
          }}
        >
          Shop with confidence.
        </Typography>
        <Typography>
          Our checkout process is pretty simple. Our checkout is safe and
          secure. Your personal and payment information is securely transmitted
          via strong encryption. We do not store any payment card information on
          our systems.
        </Typography>
      </Box>
      <Button onClick={() => handleClick(false)} sx={{ color: "#ff375e" }}>
        <NavigateBeforeIcon sx={{ fontSize: "30px" }} /> Go Back
      </Button>
    </>
  );
};

export default DeliveryPayment;
