import React, { useState } from "react";
import PlanCards from "../components/PlanCards";
import FancyNumber from "../components/FancyNumber";
import PickNumber from "../components/PickNumber";
import { Box, Typography } from "@mui/material";

function PostpaidPlan() {
  const [planName, setPlanName] = useState("");
  const [planPrice, setPlanPrice] = useState(0);

  const handleSelctedPlan = (plan, price) => {
    setPlanName(plan);
    setPlanPrice(price);
  };

  return (
    <>
      <PlanCards handleSelctedPlan={handleSelctedPlan} />
      <FancyNumber />
      <PickNumber />

      <Box className="plans-footer">
        {planName}
        <Typography sx={{ color: "#5d6971", fontSize: "16px" }}>
          BD {planPrice}
        </Typography>
        <Typography sx={{ color: "#5d6971", fontSize: "16px" }}>
          38249780
        </Typography>
      </Box>
    </>
  );
}

export default PostpaidPlan;
