import { Box, Button, Typography } from "@mui/material";
import React from "react";
import HexagonIcon from "@mui/icons-material/Hexagon";
import Divider from "@mui/material/Divider";
import { Link } from "react-router-dom";
import InfoOutlinedIcon from "@mui/icons-material/InfoOutlined";

const FancyNumber = () => {
  return (
    <Box className="fancy-number">
      <Typography variant="h4">We picked this fancy number for you</Typography>
      <Typography sx={{ color: "#5d6971", fontSize: "16px" }}>
        Your unforgettable number journey starts here
      </Typography>
      <Box className="fancy-number-content">
        <Box className="fancy-number-box">
          33049184
          <Box className="number-tag">
            <HexagonIcon sx={{ margin: "0 10px" }} />
            Bronze
          </Box>
        </Box>
        <Divider sx={{ margin: "25px 0" }}>OR</Divider>
        <Button className="primary-button">I want a better number</Button>
        <Typography className="learn-more-number">
          Still not convinced?{" "}
          <Link to="/">
            <InfoOutlinedIcon className="info-icon" /> Learn more about Vanity
            Numbers
          </Link>
        </Typography>
      </Box>
    </Box>
  );
};

export default FancyNumber;
