import { Box, Button, ButtonGroup, Typography } from "@mui/material";
import React, { useState } from "react";
import LocalShippingOutlinedIcon from "@mui/icons-material/LocalShippingOutlined";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import Stack from "@mui/material/Stack";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import Checkbox from "@mui/material/Checkbox";
import DoneIcon from "@mui/icons-material/Done";

const DeliveryDetails = () => {
  const [showAddress, setShowAddress] = useState(false);
  const [showDelivery, setShowDelivery] = useState(true);
  const [addAddress, setAddAddress] = useState(false);
  const [selectedAddress, setSelectedAddress] = useState("");
  const [delivery, setDelivery] = useState("");
  const [blockError, setBlockError] = useState(false);
  const [streetError, setStreetError] = useState(false);
  const [buildingError, setBuildingError] = useState(false);
  const [floorError, setFloorError] = useState(false);
  const [address, setAddress] = useState([]);
  const [values, setValues] = useState({
    id: "",
    buildingNo: "",
    blockNumber: "",
    street: "",
    floor: "",
    addressTag: "",
    checked: false,
    type: "",
    city: "",
  });

  const handleSubmit = (event) => {
    event.preventDefault();
    //console.log(blockNumber, street, buildingNo, floor, checked, type, city);

    if (values.blockNumber === "") {
      setBlockError(true);
    }
    if (values.street === "") {
      setStreetError(true);
    }
    if (values.buildingNo === "") {
      setBuildingError(true);
    }
    if (values.floor === "") {
      setFloorError(true);
    }

    if (
      values.blockNumber.trim() === "" ||
      values.street.trim() === "" ||
      values.floor.trim() === "" ||
      values.buildingNo.trim() === ""
    ) {
      return;
    }

    console.log(values);

    setValues({
      ...values,
      buildingNo: values.buildingNo,
      blockNumber: values.blockNumber,
      street: values.street,
      floor: values.floor,
      checked: values.checked,
      addressTag: values.addressTag,
      type: values.type,
      city: values.city,
    });

    setAddress([...address, values]);
    setAddAddress(false);

    setValues({
      buildingNo: "",
      blockNumber: "",
      street: "",
      floor: "",
      addressTag: "",
      checked: false,
      type: "",
      city: "",
    });
  };

  const handleSelectChange = (event) => {
    setValues({ ...values, type: event.target.value });
  };

  const handleCityChange = (event) => {
    setValues({ ...values, city: event.target.value });
  };

  const handleCheckChange = (event) => {
    setValues({ ...values, checked: event.target.value });
  };

  const fillForm = (add, index) => {
    setAddAddress(true);
    setValues({
      ...values,
      buildingNo: add.buildingNo,
      blockNumber: add.blockNumber,
      street: add.street,
      floor: add.floor,
      checked: add.checked,
      addressTag: add.addressTag,
      type: add.type,
      city: add.city,
    });
  };

  // const fillForm = (add, index) => {
  //   const updatedAddress = address.map(
  //     (val, i) => {
  //       if (i === index) {
  //         setBlock(val.blockNumber);
  //         setStreet(val.street);
  //         setBuilding(val.buildingNumber);
  //         setFloor(val.floor);
  //       }
  //     }
  //     //i === index ? { ...form, [blockNumber]: add.blockNumber } : form
  //   );
  //   //setFormList(updatedFormList);
  //   console.log(updatedAddress);
  //   //setAddress(updatedAddress);

  //   // setAddress(
  //   //   address.map((val, i) => {
  //   //     if (i === index) {
  //   //       setBlock(val.blockNumber);
  //   //       setStreet(val.street);
  //   //       setBuilding(val.buildingNumber);
  //   //       setFloor(val.floor);
  //   //     }
  //   //     return val;
  //   //   })
  //   // );
  //   // setBlock(address.blockNumber);
  //   // setStreet(address.street);
  //   // setBuilding(address.buildingNumber);
  //   // setFloor(address.floor);
  //   //console.log(address);
  // };

  const addressLabel = (address, index) => {
    return (
      <>
        <div
          className={address.addressTag ? "address-default" : "address-edit"}
        >
          {address.addressTag}
          <span onClick={() => fillForm(address, index)}>EDIT</span>
        </div>
        <div className="address-value">
          {`${address.buildingNo}, Road #${address.street}, Block #${address.blockNumber}, ${address.city} - Bahrain`}
        </div>
      </>
    );
  };

  const handleAddress = (delivery) => {
    setShowDelivery(false);
    setShowAddress(true);

    const deliveryValue = delivery.split(",");
    setDelivery(deliveryValue);
  };

  const handleDelivery = () => {
    setShowDelivery(true);
    setShowAddress(false);
  };

  const handleAddAddress = () => {
    setAddAddress(true);

    setValues({
      buildingNo: "",
      blockNumber: "",
      street: "",
      floor: "",
      addressTag: "",
      checked: false,
      type: "",
      city: "",
    });
  };

  const handleAddressChange = (event) => {
    setSelectedAddress(event.target.value);
  };

  const styles = {
    deliveryBox: {
      border: "1px solid #5d696f",
      padding: "20px",
      borderRadius: "10px",
      display: "flex",
      justifyContent: "space-between",
      margin: "24px 30px 0",
      cursor: "pointer",
    },
    selectedBox: {
      border: "1px solid #ff375e",
    },
    doneIcon: {
      position: "absolute",
      right: "0",
      width: "14px",
      background: "#ff375e",
      borderBottomLeftRadius: "8px",
      borderTopRightRadius: "8px",
      color: "#fff",
      height: "14px",
    },
  };
  return (
    <>
      {showDelivery && (
        <Box>
          <Box
            style={styles.deliveryBox}
            onClick={() => handleAddress("Bullet, 2")}
          >
            <Box
              sx={{ display: "flex", color: "#4f008c", alignItems: "center" }}
            >
              <LocalShippingOutlinedIcon />
              <Typography
                sx={{
                  fontSize: "12px",
                  fontWeight: "bold",
                  marginLeft: "10px",
                }}
              >
                BULLET DELIVERY
              </Typography>
            </Box>
            <Typography sx={{ fontSize: "16px" }}>
              <strong>Delivered to your doorsteps</strong> (within 2 hours)
            </Typography>
          </Box>
          <Box
            style={styles.deliveryBox}
            onClick={() => handleAddress("Standard, 24")}
          >
            <Box
              sx={{ display: "flex", color: "#4f008c", alignItems: "center" }}
            >
              <LocalShippingOutlinedIcon />
              <Typography
                sx={{
                  fontSize: "12px",
                  fontWeight: "bold",
                  marginLeft: "10px",
                }}
              >
                STANDARD DELIVERY
              </Typography>
            </Box>
            <Typography sx={{ fontSize: "16px" }}>
              <strong>Delivered to your doorsteps</strong> (within 24 hours)
            </Typography>
          </Box>
        </Box>
      )}

      {showAddress && (
        <Box>
          <Box
            style={{ ...styles.deliveryBox, ...styles.selectedBox }}
            onClick={handleDelivery}
          >
            <Box
              sx={{ display: "flex", alignItems: "center", flexWrap: "wrap" }}
            >
              <Typography sx={{ fontSize: "19px", fontWeight: "bold" }}>
                How do you want to receive your items?
              </Typography>
              <Box
                sx={{ display: "flex", color: "#4f008c", alignItems: "center" }}
              >
                <LocalShippingOutlinedIcon />
                <Typography
                  sx={{
                    fontSize: "12px",
                    fontWeight: "bold",
                    marginLeft: "10px",
                    textTransform: "uppercase",
                  }}
                >
                  {delivery[0]} DELIVERY
                </Typography>
              </Box>
            </Box>
            <Box
              sx={{
                display: "flex",
                justifyContent: "flex-end",
                flexWrap: "wrap",
              }}
            >
              <Typography
                sx={{
                  fontSize: "14px",
                  fontWeight: "bold",
                  textTransform: "uppercase",
                  color: "#ff375e",
                }}
              >
                Change Delivery
              </Typography>
              <Typography sx={{ fontSize: "14px" }}>
                <strong>Delivered to your doorsteps</strong> (within{" "}
                {delivery[1]} hours)
              </Typography>
            </Box>
          </Box>

          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              padding: "20px 30px 15px",
              borderBottom: "1px solid #eee",
              alignItems: "center",
            }}
          >
            <Typography>Where do you want to receive your items</Typography>
            <Box>
              <Button className="primary-button" onClick={handleAddAddress}>
                Add another address
              </Button>
            </Box>
          </Box>

          {addAddress && (
            <Stack
              component="form"
              spacing={2}
              autoComplete="off"
              sx={{ maxWidth: "340px", margin: "40px auto" }}
              onSubmit={handleSubmit}
              className="delivery-form"
            >
              <FormControl>
                <Select
                  id="select"
                  value={values.type}
                  IconComponent={KeyboardArrowDownIcon}
                  onChange={handleSelectChange}
                  displayEmpty
                  inputProps={{ "aria-label": "Without label" }}
                >
                  <MenuItem value="">Choose your address type</MenuItem>
                  <MenuItem value="Apartment">Apartment</MenuItem>
                  <MenuItem value="House/Villa">House/Villa</MenuItem>
                  <MenuItem value="Office">Office</MenuItem>
                </Select>
              </FormControl>

              <TextField
                id="filled-basic"
                label="Block Number"
                variant="standard"
                inputProps={{
                  inputMode: "numeric",
                  pattern: "[0-9]*",
                }}
                InputLabelProps={{ shrink: true }}
                onChange={(e) =>
                  setValues({ ...values, blockNumber: e.target.value })
                }
                error={blockError}
                value={values.blockNumber}
              />
              <TextField
                id="filled-basic"
                label="Street"
                variant="standard"
                inputProps={{
                  inputMode: "numeric",
                  pattern: "[0-9]*",
                }}
                InputLabelProps={{ shrink: true }}
                onChange={(e) =>
                  setValues({ ...values, street: e.target.value })
                }
                error={streetError}
                value={values.street}
              />
              <TextField
                id="filled-basic"
                label="House / Building Number"
                variant="standard"
                inputProps={{
                  inputMode: "numeric",
                  pattern: "[0-9]*",
                }}
                InputLabelProps={{ shrink: true }}
                onChange={(e) =>
                  setValues({ ...values, buildingNo: e.target.value })
                }
                error={buildingError}
                value={values.buildingNo}
              />
              <TextField
                id="filled-basic"
                label="Number / Floor"
                variant="standard"
                inputProps={{
                  inputMode: "numeric",
                  pattern: "[0-9]*",
                }}
                InputLabelProps={{ shrink: true }}
                onChange={(e) =>
                  setValues({ ...values, floor: e.target.value })
                }
                error={floorError}
                value={values.floor}
              />
              <FormControl>
                <Select
                  id="select-city"
                  value={values.city}
                  IconComponent={KeyboardArrowDownIcon}
                  onChange={handleCityChange}
                  displayEmpty
                  inputProps={{ "aria-label": "Without label" }}
                >
                  <MenuItem value="">City</MenuItem>
                  <MenuItem value="A'ali">A'ali</MenuItem>
                  <MenuItem value="Juffair">Juffair</MenuItem>
                  <MenuItem value="Manama">Manama</MenuItem>
                  <MenuItem value="Riffa">Riffa</MenuItem>
                </Select>
              </FormControl>
              <TextField
                id="filled-basic"
                label="Address tagged as"
                variant="standard"
                onChange={(e) =>
                  setValues({ ...values, addressTag: e.target.value })
                }
              />
              <FormControlLabel
                control={<Checkbox />}
                label="Make this as my primary address"
                onChange={handleCheckChange}
              />
              <ButtonGroup orientation="vertical">
                <Button
                  type="submit"
                  key="save"
                  className="primary-button"
                  sx={{ mb: 2 }}
                >
                  Save
                </Button>
                <Button
                  key="cancel"
                  className="primary-button"
                  onClick={() => setAddAddress(false)}
                >
                  Cancel
                </Button>
              </ButtonGroup>
            </Stack>
          )}

          <FormControl sx={{ width: "100%" }}>
            <RadioGroup
              row
              aria-labelledby="row-radio-buttons"
              name="row-radio-buttons"
              className="custom-radio address-radio"
              onChange={handleAddressChange}
              value={selectedAddress}
            >
              <Grid container sx={{ padding: "15px 30px" }} spacing={2}>
                {address?.map((val, index) => (
                  <Grid
                    item
                    xs={6}
                    sx={{
                      display: "flex",
                      gap: 2,
                      position: "relative",
                    }}
                    key={index}
                  >
                    <FormControlLabel
                      value={index}
                      control={<Radio />}
                      label={addressLabel(val, index)}
                      sx={{ width: "100%" }}
                    />
                    {selectedAddress == index && (
                      <DoneIcon style={styles.doneIcon} />
                    )}
                  </Grid>
                ))}
                {/* <Grid
                item
                xs={6}
                sx={{
                  display: "flex",
                  gap: 2,
                  position: "relative",
                }}
              >
                {address?.map((val, index) => {
                  <FormControlLabel
                    value={val.street}
                    control={<Radio />}
                    label={val.blockNumber}
                    sx={{ width: "100%" }}
                  />;
                })}
              </Grid> */}
                {/* <Grid
                item
                xs={6}
                sx={{
                  display: "flex",
                  gap: 2,
                  position: "relative",
                }}
              >
                <FormControlLabel
                  value="male"
                  control={<Radio />}
                  label="Male"
                  sx={{ width: "100%" }}
                />
              </Grid>
              <Grid
                item
                xs={6}
                sx={{
                  display: "flex",
                  gap: 2,
                  position: "relative",
                }}
              >
                <FormControlLabel
                  value="males"
                  control={<Radio />}
                  label="Males"
                  sx={{ width: "100%" }}
                />
              </Grid> */}
              </Grid>
            </RadioGroup>
          </FormControl>
        </Box>
      )}
    </>
  );
};

export default DeliveryDetails;
